import React from "react"
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

class Screens extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            photoIndex: 0,
            isOpen: false,
            images: []
        };
    }

    componentDidMount() {
        let tmpArr = []
        this.props.data.screens.forEach(element => {
            tmpArr.push(element.image)
        });
        this.setState({ images: tmpArr })
    }

    openGallery(index) {
        this.setState({ photoIndex: index })
        this.setState({ isOpen: true })
    }

    render() {
        const { photoIndex, isOpen, images } = this.state;
        const {data} = this.props
        return (
            <section className="screens" id="kokoko">
                <div className="container">
                    <h2 className="screens__title title">{data.title}</h2>
                    <div className="screens__list">
                        {
                           data.screens.map((item, index) => {
                                return <div key={index} className="screens__list__item">
                                    <img src={item.image} alt='' className="screens__list__item__image" onClick={() => this.openGallery(index)} />
                                </div>;
                            })
                        }
                    </div>
                    {isOpen && (
                        <Lightbox
                            mainSrc={images[photoIndex]}
                            nextSrc={images[(photoIndex + 1) % images.length]}
                            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                            onCloseRequest={() => this.setState({ isOpen: false })}
                            onMovePrevRequest={() =>
                                this.setState({
                                    photoIndex: (photoIndex + images.length - 1) % images.length,
                                })
                            }
                            onMoveNextRequest={() =>
                                this.setState({
                                    photoIndex: (photoIndex + 1) % images.length,
                                })
                            }
                        />
                    )}
                </div>
            </section>
        );
    }
}

export default Screens