import React from "react"

export default props => <section className="about" id="about">
    <div className="container">
        <h1 className="about__title title">{props.data.title}</h1>
        <div className="about__content">
            <div className="about__img-container">
                <img src={props.data.image} alt="" className="about__img" />
            </div>
            <p className="about__description text">{props.data.description}</p>
        </div>
    </div>
</section>