import React from "react"

export default props => <section className="footer">
    <div className="container">
        <div className="footer__list">
            {
                props.data.images.map((item, index)  => {
                    return <div key={index} className="footer__list__item">
                        <img src={item.src} alt={item.alt} className="footer__list__item__image"/>
                    </div>;
                })
            }
        </div>
            <h3 className="footer__text">{props.data.title}</h3>
    </div>
</section>