import React from "react"
import Item from "../components/characters-item"

export default props => <section className="characters" id="characters">
    <div className="container">
        <h2 className="characters__title title">{props.data.title}</h2>
        <div className="characters__cards-container">
        {
            props.data.characters.map((item, index) => {
                return <Item key={index}
                number={index}
                    data={item} />;
            })
        }
        </div>
    </div>
</section>