import React from "react"
import { Link } from "react-scroll"
import { Link as GLink  } from "gatsby"
import music from "../assets/music/Enigma.mp3"

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLocalesOpened: false,
            isMenuOpened: false
        };
    }

    toggleLocales() {
        this.setState({ isLocalesOpened: !this.state.isLocalesOpened })
    }

    handleSetActive(link) {
        window.location.hash = link
    }

    toggleMusic() {
        const player = document.getElementById('player')
        if (player) {
            if (player.paused) {
                player.play()
            } else {
                player.pause()
            }
        }
    }

    toggleMenu() {
        this.setState({ isMenuOpened: !this.state.isMenuOpened })
    }

    closeMenu() {
        this.setState({ isMenuOpened: false })
    }

    render() {
        return (
            <header className="header">
                <div className="container header__container">
                    <a href="#" className={`nav__trigger ${this.state.isMenuOpened ? 'is-active' : null}`} onClick={() => this.toggleMenu()}>
                        <div className="bars"></div>
                    </a>
                    <ul className={`nav__list ${this.state.isMenuOpened ? 'is-active' : null}`}>
                        {
                            this.props.data.nav.map((item, index) => {

                                return <li key={index} className="nav__list__item">
                                    <Link
                                        activeClass="active"
                                        to={item.link}
                                        spy={true}
                                        hashSpy={true}
                                        smooth={true}
                                        duration={500}
                                        offset={-50}
                                        onClick= {() => this.closeMenu()}
                                    >{item.text ? item.text : null}</Link>
                                </li>;
                            })
                        }
                    </ul>
                    <div className="header__settings">
                        
                        <div className="header__sound__container">
                            <img src={this.props.data.soundImage} onClick={() => this.toggleMusic()} alt="toggle sound" className="header__sound__image" />
                            <audio autoPlay id="player">
                                <source src={music} type="audio/mpeg" />
                            </audio>
                        </div>
                        <div className="header__local__container">
                            <div className="local__active" onClick={() => this.toggleLocales()}>{this.props.data.currentLocale}</div>
                            {this.state.isLocalesOpened ? (<ul className="local__list">
                                {
                                    this.props.data.locales.map((item, index) => {
                                        return <li key={index} className="local__list__item"><GLink to={item.link}>{item.text}</GLink>
                                        </li>;
                                    })
                                }
                            </ul>) : null}
                        </div>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header