import React from "react"

export default props => <div className={props.number % 2 !== 1 ? "characters-item" : "characters-item characters-item_reverse"}>
    <div className="characters-item__card">
        <div className="characters-item__img-part characters-item__part">
            <img src={props.data.image} alt="" className="characters-item__img" />
        </div>
        <div className="characters-item__content-part characters-item__part">
            <div className="characters-item__layout"></div>
            <h4 className="characters-item__title">{props.data.title}</h4>
            <p className="characters-item__subtitle">{props.data.subtitle}</p>
            <div className="divider"></div>
            <p className="characters-item__description">{props.data.description}</p>
        </div>
    </div>

</div>